  	line-height: 60px;
  	text-align: center;
    color:#fff;
    border-radius: 5px;
    margin: 0 auto;
  }
  .icon-tit{
  	margin-top: 5px;
  	height: 25px;
  	line-height: 25px;
  	font-size: 1.2rem;
  	text-align: center;
  }
</style>


