import Vue from 'vue'
import Router from 'vue-router'//引入vue路由
import index from '../components/index'//主界面
import hospitalIncome from '../components/hospitalIncome'//医疗收入
import inpatientDepartmentsIncome from '../components/inpatientDepartmentsIncome'//门诊科室收入
import outpatientDepartmentsIncome from '../components/outpatientDepartmentsIncome'//门诊科室收入
import registeredPersons from '../components/registeredPersons'//挂号人次
import furtherConsultation from '../components/furtherConsultation'//复诊人次
import runSide from '../components/runSide'//跑方人次
import leaveHospital from '../components/leaveHospital'//入出院人次
import drugProportions from '../components/drugProportions'//药品占比
import hospitalizationExpenses from '../components/hospitalizationExpenses'//住院费用
import bedDay from '../components/bedDay'//住院床日
import hospitalizationRate from '../components/hospitalizationRate'//住院率
import drugRanking from '../components/drugRanking'//药品排行
import diseaseRanking from '../components/diseaseRanking'//病种排行
import inspectionRanking from '../components/inspectionRanking'//检验排行
import checkRanking from '../components/checkRanking'//检查排行
import '@/assets/font.css'//引入iconfont图标
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: index
    },
    {
      path: '/hospitalIncome',
      component: hospitalIncome
    },
    {
      path: '/inpatientDepartmentsIncome',
      component: inpatientDepartmentsIncome
    },
    {
      path: '/outpatientDepartmentsIncome',
      component: outpatientDepartmentsIncome
    },
    {
      path: '/registeredPersons',
      component: registeredPersons
    },
    {
      path: '/furtherConsultation',
      component: furtherConsultation
    },
    {
      path: '/runSide',
      component: runSide
    },
    {
      path: '/leaveHospital',
      component: leaveHospital
    },
    {
      path: '/drugProportions',
      component: drugProportions
    },
    {
      path: '/hospitalizationExpenses',
      component: hospitalizationExpenses
    },
    {
      path: '/bedDay',
      component: bedDay
    },
    {
      path: '/hospitalizationRate',
      component: hospitalizationRate
    },
    {
      path: '/drugRanking',
      component: drugRanking
    },
    {
      path: '/diseaseRanking',
      component: diseaseRanking
    },
    {
      path: '/inspectionRanking',
      component: inspectionRanking
    }
    ,
    {
      path: '/checkRanking',
      component: checkRanking
    }
  ]
})



