<template>
	<div id="leaveHospital">
		<div class="detailsTitle"><i class="el-icon-arrow-left" @click="toHome"></i><p class="big-title">入出院人次</p></div>
		<div class="title"><span class="tit-span">入出院人次查询</span>
          <!--<span class="tit-time">2019-07-04</span>-->
		</div>
		<div class="actual-div">
			<el-row>
				<el-col :span="12">
					  <div class="pickerDiv">
				        <div class="showTime" @click="selectData1">
				            <p class="timeDes">开始时间：{{this.selectedValue1}}</p>
				        </div>
				        <!-- @touchmove.prevent 阻止默认事件，此方法可以在选择时间时阻止页面也跟着滚动。 -->
				        <div class="pickerPop" @touchmove.prevent>
				            <!-- 年月日时分选择 -->
				            <mt-datetime-picker
				                lockScroll="true"
				                ref="datePicker1"
				                v-model="dateVal1"
				                class="myPicker"
				                type="date"
				                year-format="{value}"
				                month-format="{value}"
				                date-format="{value}"
				                @confirm="dateConfirm1()">
				            </mt-datetime-picker>
				        </div>
				    </div>
				</el-col>
				<el-col :span="12">
					  <div class="pickerDiv">
				        <div class="showTime" @click="selectData2">
				            <p class="timeDes">结束时间：{{this.selectedValue2}}</p>
				        </div>
				        <!-- @touchmove.prevent 阻止默认事件，此方法可以在选择时间时阻止页面也跟着滚动。 -->
				        <div class="pickerPop" @touchmove.prevent>
				            <!-- 年月日时分选择 -->
				            <mt-datetime-picker
				                lockScroll="true"
				                ref="datePicker2"
				                v-model="dateVal2"
				                class="myPicker"
				                type="date"
				                year-format="{value}"
				                month-format="{value}"
				                date-format="{value}"
				                @confirm="dateConfirm2()">
				            </mt-datetime-picker>
				        </div>
				    </div>
				</el-col>
			</el-row>
		</div>
		<el-button type="primary" icon="el-icon-search" id="cxbtn" style="margin-left: 10px;" @click="getData()">查询</el-button>
		<div class="actual-div">
			<el-row>
			  <el-col :span="8"><div class="grid-content">
				<p class="actual-tit">入院人次</p>
				<p class="actual-num actual-color-deft" id="rydiv"></p>
			  </div></el-col>
			  <el-col :span="8"><div class="grid-content">
				<p class="actual-tit">出院人次</p>
				<p class="actual-num actual-color-link" id="cydiv"></p>
			  </div></el-col>
			  <el-col :span="8"><div class="grid-content">
				<p class="actual-tit">当前在院</p>
				<p class="actual-num actual-color" id="zydiv"></p>
			  </div></el-col>
			</el-row>
		</div>
		<div class="jg-div"></div>
		<div class="title"><span class="tit-span">入出院人次分析</span>
		</div>
		
		<div id="chart" ref="chart"></div>
	</div>
	
</template>

<script>
	import Vue from 'vue'
	import $ from 'jquery'
	import echarts from "echarts"  //引入echarts
	import { Loading } from 'element-ui';
	import { DatetimePicker } from 'mint-ui'		
    import {formatDate} from '@/assets/js/formatdata.js'
    Vue.component(DatetimePicker.name, DatetimePicker)
    let xarr;
    let jsonStr1;
    let jsonStr2;
    let series;
    let ryArr;
    let cyArr;
    let zyArr;
    var ryVal;   
	var cyVal;	
	var zyVal;
	export default {
		data() {
			return {
				commonUrl: this.GLOBAL.commonUrl,//引入接口的域名和端口
		        selectedValue1: '',
		        selectedValue2: '',
		        nowDate:'',
		        oldDate:'',
		        dateVal1: '', // 默认是当前日期
		        dateVal2: '',
 
            }
		},
		created() {
           this.getTime();
		},
		mounted() {
			this.$nextTick(() => { //初始化				
				this.getData();
			});
		},
		methods: {
			//获取当前日期
			getTime(){
				  var date = new Date();
				  var seperator1 = "-";
				  var year = date.getFullYear();
				  var month = date.getMonth() + 1;
				  var strDate = date.getDate();
				  if (month >= 1 && month <= 9) {
				    month = "0" + month;
				  }
				  if (strDate >= 0 && strDate <= 9) {
				    strDate = "0" + strDate;
				  }
				  this.selectedValue2 = year + seperator1 + month + seperator1 + strDate;
				  //获取当前月份
				  this.selectedValue3 = year + seperator1 + month+ seperator1 + strDate;
				  //获取系统前一周的时间
				var oneweekdate = new Date(date-7*24*3600*1000);
				var y = oneweekdate.getFullYear();
				var m = oneweekdate.getMonth()+1;
				var d = oneweekdate.getDate();
				if (m >= 1 && m <= 9) {
				    m = "0" + m;
				  }
				  if (d >= 0 && d <= 9) {
				    d = "0" + d;
				  }
				this.selectedValue1 = y + seperator1 + m + seperator1 + d;
				//获取五月前的一号
				var yueNum=date.getMonth() -4;
				  if(yueNum<=0&&yueNum>-3){
				  	yueNum=yueNum+12;
				  }
				  if(yueNum<=-3){
				  	yueNum=yueNum+12;
				  	yueNum="0"+yueNum;
				  }
				  if(yueNum>0){
				  	yueNum="0"+yueNum;
				  }
				  this.nowDate=year + seperator1 + month + seperator1 + strDate;
				  this.oldDate=year + seperator1 + yueNum+seperator1+"01";
			},
			//开始时间
			selectData1 () { // 打开时间选择器
	            // 如果已经选过日期，则再次打开时间选择器时，日期回显（不需要回显的话可以去掉 这个判断）
	            if (this.selectedValue1) {
	                this.dateVal1 = this.selectedValue1
	            } else {
	                this.dateVal1 = new Date()
	            }
	            this.$refs['datePicker1'].open()
	        },
	        dateConfirm1 () { // 时间选择器确定按钮，并把时间转换成我们需要的时间格式
	            this.selectedValue1 = formatDate(this.dateVal1)
	            console.log(this.selectedValue1)
	        },
	        //结束时间
	        selectData2 () { // 打开时间选择器
	            // 如果已经选过日期，则再次打开时间选择器时，日期回显（不需要回显的话可以去掉 这个判断）
	            if (this.selectedValue2) {
	                this.dateVal2 = this.selectedValue2
	            } else {
	                this.dateVal2 = new Date()
	            }
	            this.$refs['datePicker2'].open()
	        },
	        dateConfirm2 () { // 时间选择器确定按钮，并把时间转换成我们需要的时间格式
	            this.selectedValue2 = formatDate(this.dateVal2)
	            console.log(this.selectedValue2)
	        },
	        
			//跳转主页
            toHome(){
	           if (window.history.length <= 1) {
	                this.$router.push({path:'/'})
	                return false
	            } else {
	                this.$router.go(-1)
	            }	
	        },	
	        getData(){
	        	this.initChart();
				this.showData();
				//this.showData2();
				window.addEventListener("resize", () => {
					this.resize(); //监听屏幕大小，来刷新画布
				});
	        },
	        showData(){
	        	//this.fullscreenLoading = true;
	        	var url1=this.commonUrl+'/Data/FindGet?param={"报表名称":"BI103_入出院人次.xml","开始时间":"' + this.selectedValue1 + '","结束时间":"' + this.selectedValue2 + '","科室":"所有"}'
	        	//var url="http://localhost:5113/MedicalQualityAjax/BedDayAssessment"
	        	var url=decodeURI(url1);
	        	console.log(url);
                $.ajax({
		          url: url,
		          type: 'GET',
		          dataType: 'JSONP',		          
				  jsonp: "callback",	          
		          success: function (res) {
		          	//this.fullscreenLoading = false;
		          	console.log(res.数据集[0]);
		          	if(res.数据集.length>0){
		          		$("#rydiv").html(res.数据集[0].入院人数);
			            $("#cydiv").html(res.数据集[0].出院人数);
			            $("#zydiv").html(res.数据集[0].当前在院);
		          	}else{
		          		$("#cydiv").html("当前无数据");
		          	}
		            
		          }
		        });
		        
	        },
		    //初始化图表
		    initChart() {
		    	//定义加载层
		    	const loading = this.$loading({
		          lock: true,
		          text: '数据拼命加载中',
		          spinner: 'el-icon-loading',
		          background: 'rgba(0, 0, 0, 0.7)'
		        });
//		    	 var ksVal=$("#itemSelect").val();
//		    	if(ksVal==null){
//	        		ksVal="所有";
//	        	}
		    	 // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('chart'));
                var url1=this.commonUrl+'/Data/FindGet?param={"报表名称":"BI106_入出院人次逐月分析.xml","开始时间":"' + this.oldDate + '","结束时间":"' + this.nowDate + '"}';
	        	var url=decodeURI(url1);
	        	console.log(url);
            $.ajax({
	          url: url,
	          type: 'GET',
	          dataType: 'JSONP',		          
			  jsonp: "callback",
	          success: function (res) {  	          	
			  console.log(res);
			  let jsonStr1=res.数据集;
				xarr = [];
				ryArr=[];
				series=[];
				cyArr=[];
				$.each(jsonStr1, function(n, val) {							
							series.push({
								name:val.日期,
				                value: Number(val.入院人数),
				                fzvalue: Number(val.出院人数),
				            }); 
				});
				$.each(series, function (n, value) {
				      xarr.push(value.name);
				});
				$.each(series, function (n, value) {
				      ryArr.push(value.value);
				});
				$.each(series, function (n, value) {
				      cyArr.push(value.fzvalue);
				});
				//关闭加载层
				loading.close();
					// 绘制图表
                myChart.setOption({
                    color:['#7f59d3','#f3504b','#ffc82d'],
				    tooltip : {
				        trigger: 'axis',
				        axisPointer: {
				            type: 'cross',
				            label: {
				                backgroundColor: '#6a7985'
				            }
				        }
				    },
				    legend: {
								data: ['入院人次', '出院人次']
							},
				    grid: {
				        left: '3%',
				        right: '4%',
				        bottom: '3%',
				        top:'10%',
				        containLabel: true				        
				    },
				    xAxis : [
				        {
				            type : 'category',
				            boundaryGap : false,
				            data : xarr,
				            axisLabel: {
                                           interval:0,
                                           rotate:40
                                        }
				        }
				    ],
				    yAxis : [
				        {
				            type : 'value',
				            axisLabel:{
				            	formatter: function(params) {
										return params;
									}
				            }
				        }
				    ],
				    series : [
				        {
				            name:'入院人次',
				            type:'line',
				            areaStyle: {},
				            data:ryArr,
				            label: {
				                normal: {
				                    show: true,
				                    position: 'top',
									formatter: function(params) {
										return params.value;
									}
				                }
				            },
				            areaStyle: {
				              normal: {
				                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
				                  { offset: 0, color: "#a896d0" },
				                  { offset: 0.5, color: "#c4bfcd" },
				                  { offset: 1, color: "#fff" }
				                ])
				              }
				            }
				        },
				        {
				            name:'出院人次',
				            type:'line',
				            areaStyle: {},
				            data:cyArr,
				            label: {
				                normal: {
				                    show: true,
				                    position: 'top',
									formatter: function(params) {
										return params.value;
									}
				                }
				            },
				            areaStyle: {
				              normal: {
				                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
				                  { offset: 0, color: "#ed9590" },
				                  { offset: 0.5, color: "#edc4c1" },
				                  { offset: 1, color: "#fff" }
				                ])
				              }
				            }
				        }
				    ]
               });	
				}
		      });
			  myChart.resize(); //刷新画布
			  myChart.on('click', function (params) {
              });
		      },		      
		    resize() {
		      if (this.chart) {
		        this.chart.resize();
		      }
		      if (this.chart2) {
		        this.chart2.resize();
		      }
		     }
		    },
		  computed: {},
		  watch: {}   
	}
</script>

<style>
	.detailsTitle {
		background-color: #4788d8;
		color: #fff;
		line-height: 60px;
		height: 60px;
		position: relative;
	}
	
	.big-title{
		text-align: center;
		display: inline-block;
		margin: 0 auto;
		width: 100%;
		font-size: 1.8rem;
	}
	.detailsTitle .el-icon-arrow-left{
		position: absolute;
		left: 10px;
		top: 18px;
		font-size: 2.4rem;
	}
	.detailsTitle .el-icon-setting{
		position: absolute;
		right: 10px;
		top: 18px;
		font-size: 2.4rem;
	}
	.title{
		width: 100%;
		height: 50px;
		line-height: 50px;
		font-size: 1.6rem;
		font-family: "微软雅黑";
		border-bottom:1px solid #cecece;
	}
	.tit-span{
		margin-left: 10px;
	}
	.tit-time{
		margin-left: 20px;
		font-size: 1.4rem;
		color: #cecece;
	}
	  .el-row {
    margin-top:10px; 
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .actual-tit,.actual-num{
  	width: 100%;
  	font-size: 1.5rem;
  	text-align: center;
  	height: 30px;
  	line-height: 30px;
  }
  .actual-num{
  	font-size: 1.8rem;
  }
  .actual-color-dark {
    color: #ffc82d;
  }
  .actual-color {
    color: #5a7ef8;
  }
  .actual-color-light {
    color: #00c37f;
  }
  .actual-color-link{
  	color: #f3504b;
  }
  .actual-color-deft{
  	color: #7f59d3;
  }
  #chart {
  width: 100%;
  height: 400px;
}
#chart2 {
  width: 100%;
  height: 400px;
}
#chart3 {
  width: 100%;
  height: 400px;
}
  .jg-div{
  	height: 15px;
  	background: #f2f1f6;
  }
  .timeText{
            text-align: center;
        }
        .pickerDiv{
        	height: 30px;
        	margin-left: 10px;
        	font-size: 1.6rem;
        }
</style>


