<template>
	<div id="hospitalizationRate">
		<div class="detailsTitle"><i class="el-icon-arrow-left" @click="toHome"></i><p class="big-title">住院率</p></div>
		<div class="title"><span class="tit-span">全院住院率</span>
          <!--<span class="tit-time">2019-07-04</span>-->
		</div>
		<div class="actual-div">
			<el-row>
				<el-col :span="11">
					<el-date-picker v-model="value1" type="date" placeholder="选择日期"></el-date-picker>
				</el-col>
				<el-col :span="11">
					<el-date-picker v-model="value2" type="date" placeholder="选择日期"></el-date-picker>
				</el-col>
			</el-row>
		</div>
		<div class="actual-div">
			<el-row>
			  <el-col :span="24"><div class="grid-content">
	            <p class="actual-tit">全院住院率</p>
	            <p class="actual-num actual-color">0.21</p>
			  </div></el-col>
			</el-row>
		</div>
	</div>	
</template>

<script>
	import weuiJs from "weui.js"
	export default {
		data() {
			return {
              value1: '2019-07-01',
              value2: '2019-07-04'
            }
		},
		created() {},
		mounted() {
		},
		methods: {
			//跳转主页
            toHome(){
	           if (window.history.length <= 1) {
	                this.$router.push({path:'/'})
	                return false
	            } else {
	                this.$router.go(-1)
	            }	
	        },
		    },
		  computed: {},
		  watch: {}   
	}
</script>

<style>
	.detailsTitle {
		background-color: #4788d8;
		color: #fff;
		line-height: 60px;
		height: 60px;
		position: relative;
	}
	.big-title{
		text-align: center;
		display: inline-block;
		margin: 0 auto;
		width: 100%;
		font-size: 1.8rem;
	}
	.detailsTitle .el-icon-arrow-left{
		position: absolute;
		left: 10px;
		top: 18px;
		font-size: 2.4rem;
	}
	.title{
		width: 100%;
		height: 50px;
		line-height: 50px;
		font-size: 1.6rem;
		font-family: "微软雅黑";
		border-bottom:1px solid #cecece;
	}
	.tit-span{
		margin-left: 10px;
	}
	.tit-time{
		margin-left: 20px;
		font-size: 1.4rem;
		color: #cecece;
	}
	  .el-row {
    margin-top:10px; 
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .actual-tit,.actual-num{
  	width: 100%;
  	font-size: 1.5rem;
  	text-align: center;
  	height: 30px;
  	line-height: 30px;
  }
  .actual-num{
  	font-size: 1.8rem;
  }
  .actual-color-dark {
    color: #ffc82d;
  }
  .actual-color {
    color: #5a7ef8;
  }
  .actual-color-light {
    color: #00c37f;
  }
  .actual-color-link{
  	color: #f3504b;
  }
  .actual-color-deft{
  	color: #7f59d3;
  }
  #chart {
  width: 100%;
  height: 400px;
}
#chart2 {
  width: 100%;
  height: 400px;
}
#chart3 {
  width: 100%;
  height: 400px;
}
  .jg-div{
  	height: 15px;
  	background: #f2f1f6;
  }
  .timeText{
            text-align: center;
        }
        .pickerView{
            width:100%;
            height: 300rpx;
            margin-top: 100rpx;
            
        }
        .pickerColumn{
                .pickerItem{
                    line-height:68rpx;
                    height:34rpx;
                    overflow:hidden;
                    text-align: center;
                }
            }
</style>


